"use client"

import React, { useState, useEffect } from 'react'
import { Html5QrcodeScanner } from 'html5-qrcode'

export default function ScanPage() {
  const [result, setResult] = useState('')
  const [isScanning, setIsScanning] = useState(false)

  useEffect(() => {
    const scanner = new Html5QrcodeScanner(
      "qr-reader",
      { 
        fps: 10, 
        qrbox: { width: 250, height: 250 },
        aspectRatio: 1.0,
        showTorchButtonIfSupported: true,
        rememberLastUsedCamera: true
      },
      false
    )

    scanner.render(
      (decodedText) => {
        setResult(decodedText)
        setIsScanning(true)
        console.log(decodedText)
      },
      (error) => {
        console.log(error)
      }
    )

    return () => {
      scanner.clear()
    }
  }, [])

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="max-w-2xl mx-auto">
        <h1 className="text-2xl font-bold text-white mb-6">QR Code Scanner</h1>
        
        <div className="bg-[#1a1b1e] border border-gray-800 rounded-2xl p-6 shadow-xl">
          <div className="mb-4 text-gray-300 text-sm">
            {!isScanning ? "Please allow camera access and point your camera at a QR code" : "Scanning..."}
          </div>
          
          <div id="qr-reader" className="rounded-xl overflow-hidden" />
          
          {result && (
            <div className="mt-4 p-4 bg-white/5 rounded-xl">
              <h3 className="text-lg font-semibold text-white mb-2">Scanned Result:</h3>
              <p className="text-gray-300 break-all">{result}</p>
            </div>
          )}
        </div>

        <style jsx global>{`
          #qr-reader {
            border: none !important;
            padding: 0 !important;
            width: 100% !important;
          }
          #qr-reader__status_span {
            background: rgba(255, 255, 255, 0.1) !important;
            color: white !important;
            border: none !important;
          }
          #qr-reader__camera_selection {
            background: rgba(255, 255, 255, 0.1) !important;
            color: white !important;
            border: none !important;
            padding: 0.5rem !important;
            border-radius: 0.5rem !important;
            margin: 0.5rem 0 !important;
            width: 100% !important;
          }
          #qr-reader__camera_permission_button {
            background: rgba(255, 255, 255, 0.1) !important;
            color: white !important;
            border: none !important;
            padding: 0.75rem 1.5rem !important;
            border-radius: 0.5rem !important;
            margin: 0.5rem 0 !important;
            cursor: pointer !important;
            transition: background-color 0.2s !important;
          }
          #qr-reader__camera_permission_button:hover {
            background: rgba(255, 255, 255, 0.2) !important;
          }
          #qr-reader__dashboard {
            padding: 0 !important;
            margin: 0 !important;
            background: transparent !important;
          }
          #qr-reader__scan_region {
            background: transparent !important;
            position: relative !important;
          }
          #qr-reader__scan_region::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            border: 2px solid rgba(255, 255, 255, 0.2);
            border-radius: 0.5rem;
          }
          #qr-reader video {
            border-radius: 0.75rem !important;
          }
          #qr-reader select {
            display: none !important;
          }
          #qr-reader__dashboard_section_swaplink {
            display: none !important;
          }
        `}</style>
      </div>
    </div>
  )
} 
